.timeline-container {
  border-style: solid;
  border-radius: 1rem;
  border-width: thin;
  border-color: lightgray;
  width: 82%;
  display: flex;
  align-self: center;
  flex-direction: column;
  transition: box-shadow 0.3s ease-in-out;
  margin-top: 3%;
  padding-left: 2%;
  padding-top: 2%;
}

#experience {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-top: 1%;
  width: 90%;
  border: none;
}

#timeline {
  margin-top: 2%;
  padding-left: 4%;
}

.timeline-container:hover {
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.company-logo {
  width: 8rem;
}

#company-info {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.company-website {
  font-size: large;
  color: #575866;
  margin-left: 2%;
}

#company-name {
  color: #575866;
  width: fit-content;
  margin-left: 2%;
}

.experience-info {
  width: 100%;
  margin-left: 5%;
}

#experience-main-info {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.experience-duration {
  height: fit-content;
  display: inherit;
  align-self: center;
  font-size: medium;
  font-weight: bold;
  background-color: inherit;
}

.experience-description {
  text-align: left;
}
